
/*
================================================================================
|                                  TOOLTIPS                                    |
================================================================================
*/
.lp-input__tooltip {
    background: @tooltipBg;
    box-shadow: @tooltipShadow;
    font-size: @tooltipFontSize;
    line-height: @tooltipLineHeight;
    font-weight: @tooltipFontWeight;
    color: @tooltipTextColor;

    &::after {
        border-top: @tooltipDecorSizer solid @tooltipBg;
    }
}

.lp-registration-form {
    &--tooltip-bottom {
        .lp-input__tooltip {
            &::after {
                border-bottom: @tooltipDecorSizer solid @tooltipBg;
            }
    
            @media @screen-tablet-break, @screen-mobile-break {
                &::after {
                    border-top: @tooltipDecorSizer solid @tooltipBg;
                    border-bottom: @tooltipDecorSizer solid transparent;
                }
            }
        }
    }

    &--tooltip-right {
        .lp-input__tooltip {
            border-right: @tooltipDecorSizer solid @tooltipBg;

            @media @screen-tablet-break, @screen-mobile-break {
                &::after {
                    border-top: @tooltipDecorSizer solid @tooltipBg;
                    border-right: none;
                }
            }
        }
    }

    &--tooltip-left {
        .lp-input__tooltip {
            border-left: @tooltipDecorSizer solid @tooltipBg;

            @media @screen-tablet-break, @screen-mobile-break {
                &::after {
                    border-top: @tooltipDecorSizer solid @tooltipBg;
                    border-left: @tooltipDecorSizer solid transparent;
                }
            }
        }
    }
}