
/*
================================================================================
|                             HORIZONTAL STYLES                                |
================================================================================
*/
.lp-registration-form {
    &--horizontal {
        background: @containerHorizontalBg;
        padding: @containerHorizontalPadding;
        border-radius: @containerHorizontalBorderRadius;

		.swShortRegistrationContent {
			flex-direction: row;
            flex-wrap: wrap;
            margin: 0 -@inputGutterSide;
        }

        .swFormGroup, .lp-custom-phone-group {
            margin: 0 @inputGutterSide @inputGutterBottom @inputGutterSide;
            flex: 1 1 calc(25% - @inputGutterSide * 2);

            .swFormGroup {
                margin: @inputHorizontalMargin;
            }

            &.swCheckBoxField {
                flex: 1 1 calc(100% - @inputGutterSide * 2);
                order: 2;
                
                label {
                    margin: @checkboxHorizontalMargin;
                }
            }

            &.swActionButton {
                margin: 0 @inputGutterSide @inputGutterBottom @inputGutterSide;

                input[type="button"] {
                    height: @buttonHorizontalHeight;
                }
            }

            .swFormControl {
                margin: @inputHorizontalMargin;
            }
        }

        .lp-popup {
            &__body {
                max-width: 442px;
            }
        }

        @media @screen-tablet-break {
            .swFormGroup, .lp-custom-phone-group {
                flex: 1 1 calc(100% / 3 - @inputGutterSide * 2);

                &.swCheckBoxField {
                    order: 0;
                }

                &.swActionButton {
                    margin: @inputGutterBottom @inputGutterSide;
                }
            }
        }

        @media @screen-mobile-break {
            .swFormGroup, .lp-custom-phone-group {
                flex: 1 1 calc(100% - @inputGutterSide * 2);

                &.swCheckBoxField {
                    order: 0;
                }
            }
        }
	}
}