
/*
================================================================================
|                                    BUTTONS                                   |
================================================================================
*/
.lp-main-button, .swButtonField .swbActionButton {
    background: @buttonBg;
    box-shadow: @buttonShadow;
    border: @buttonBorder;
    font-weight: 500;
    color: @buttonTextColor;

    &:hover {
        background: @buttonBgHover;
        box-shadow: @buttonShadowHover;
    }
}