
/*
================================================================================
|                                    POPUP                                     |
================================================================================
*/
.lp-popup {
    &__body {
        background: @popupBodyBg;
        border: @popupBodyBorder;
    }

    &__content {
        color: @popupContentTextColor;
        font-weight: @popupContentFontWeight;

        a {
            color: @popupContentLinkColor;
        }
    }
}