
/*
================================================================================
|                              COMBINED STYLES                                 |
================================================================================
*/
.lp-registration-form {
    &--combined {
        .swShortRegistrationContent {
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: center;
            margin: 0 -@inputGutterSideSm;
        }

        .swFormGroup, .lp-custom-phone-group {
            margin: @inputCombinedMargin;
            flex: 1 1 calc(50% - @inputGutterSideSm * 2);

            &.swFullNameField {
                flex: 1 1 calc(100% - @inputGutterSideSm * 2);
            }

            &.swCheckBoxField {
                flex: 1 1 calc(100% - @inputGutterSideSm * 2);
                margin-bottom: @inputGutterSide;

                label {
                    margin-top: 0;
                }
            }

            &.swActionButton {
                flex: 1 1 calc(100% - @inputGutterSideSm * 2);
                margin: @buttonCombinedButtonMargin;

                input[type="button"] {
                    height: @buttonCombinedHeight;
                    font-size: @buttonCombinedFontSize;
                }
            }

            .swFormGroup {
                margin: @inputHorizontalMargin;
            }

            .swFormControl {
                margin: 0;
                height: @inputCombinedHeight;
            }
        }

        @media @screen-mobile-break {
            .swFormGroup, .lp-custom-phone-group {
                flex: 1 1 auto;

                &.swActionButton {
                    input[type="button"] {
                        height: @buttonHeight;
                        font-size: @buttonFontSize;
                    }
                }

                .swFormControl {
                   height: @inputHeight;
                }
            }
        }
    }
}