/*
================================================================================
|                                COMPONENTS                                    |
================================================================================
*/
/*
================================================================================
|                                   INPUTS                                     |
================================================================================
*/
.lp-registration-form .swFormControl {
  border-color: #999;
  color: #202428;
}
.lp-registration-form .swFormControl:hover,
.lp-registration-form .swFormControl:focus {
  border-color: #2cc4a2;
}
.lp-registration-form .swFormControl::placeholder {
  font-style: italic;
  font-weight: 600;
}
/*
================================================================================
|                                 CHECKBOX                                     |
================================================================================
*/
.lp-registration-form .swCheckBoxField label {
  color: #666;
}
.lp-registration-form .swCheckBoxField label .label-icon {
  border: 2px solid #e6e6e6;
}
.lp-registration-form .swCheckBoxField label .label-icon__inner {
  background-color: #2cc4a2;
}
.lp-registration-form .swCheckBoxField label a {
  color: #202428;
}
.lp-registration-form .swCheckBoxField label a:hover {
  color: #202428;
}
/*
================================================================================
|                                    BUTTONS                                   |
================================================================================
*/
.lp-main-button,
.swButtonField .swbActionButton {
  background: linear-gradient(90deg, #20776f, #33cca5);
  box-shadow: 0 0px 0px 0 rgba(48, 187, 154, 0.35);
  border: none;
  font-weight: 500;
  color: #fff;
}
.lp-main-button:hover,
.swButtonField .swbActionButton:hover {
  background: linear-gradient(90deg, #20776f, #33cca5);
  box-shadow: 0 6px 10px 0 rgba(48, 187, 154, 0.35);
}
/*
================================================================================
|                                  TOOLTIPS                                    |
================================================================================
*/
.lp-input__tooltip {
  background: rgba(255, 255, 255, 0.9);
  box-shadow: 0 0 30px 0 #999;
  font-size: 16px;
  line-height: 1.42;
  font-weight: 400;
  color: #202428;
}
.lp-input__tooltip::after {
  border-top: 7px solid rgba(255, 255, 255, 0.9);
}
.lp-registration-form--tooltip-bottom .lp-input__tooltip::after {
  border-bottom: 7px solid rgba(255, 255, 255, 0.9);
}
@media (min-width: 768px) and (max-width: 991px), (min-width: 320px) and (max-width: 767px) {
  .lp-registration-form--tooltip-bottom .lp-input__tooltip::after {
    border-top: 7px solid rgba(255, 255, 255, 0.9);
    border-bottom: 7px solid transparent;
  }
}
.lp-registration-form--tooltip-right .lp-input__tooltip {
  border-right: 7px solid rgba(255, 255, 255, 0.9);
}
@media (min-width: 768px) and (max-width: 991px), (min-width: 320px) and (max-width: 767px) {
  .lp-registration-form--tooltip-right .lp-input__tooltip::after {
    border-top: 7px solid rgba(255, 255, 255, 0.9);
    border-right: none;
  }
}
.lp-registration-form--tooltip-left .lp-input__tooltip {
  border-left: 7px solid rgba(255, 255, 255, 0.9);
}
@media (min-width: 768px) and (max-width: 991px), (min-width: 320px) and (max-width: 767px) {
  .lp-registration-form--tooltip-left .lp-input__tooltip::after {
    border-top: 7px solid rgba(255, 255, 255, 0.9);
    border-left: 7px solid transparent;
  }
}
/*
================================================================================
|                             CUSTOM SELECT                                    |
================================================================================
*/
.lp-registration-form .lp-custom-phone-group .lp-dropdown__flag .flag-arrow {
  border-top-color: #555;
}
.lp-registration-form .lp-custom-phone-group .lp-dropdown__flag .flag-plus {
  color: #202428;
}
/*
================================================================================
|                                TEMPLATES                                     |
================================================================================
*/
/*
================================================================================
|                               MAIN STYLES                                    |
================================================================================
*/
.lp-registration-form {
  background: #fff;
}
/*
================================================================================
|                             HORIZONTAL STYLES                                |
================================================================================
*/
/*
================================================================================
|                              COMBINED STYLES                                 |
================================================================================
*/
/*
================================================================================
|                              TRANSPARENT STYLES                              |
================================================================================
*/
.lp-registration-form--transparent {
  background-color: transparent;
}
/*
================================================================================
|                                    POPUP                                     |
================================================================================
*/
.lp-popup__body {
  background: #1b1e23;
  border: none;
}
.lp-popup__content {
  color: #fff;
  font-weight: 400;
}
.lp-popup__content a {
  color: #2cc4a2;
}
