
/*
================================================================================
|                              COMBINED STYLES                                 |
================================================================================
*/
.lp-registration-form {
    &--rtl {
        direction: rtl;
		text-align: right;

        .lp-dropdown {
            order: 1;

            &__input {
                direction: ltr;

                &__body {
                    direction:  ltr;
                    text-align: left;

                    .country-item {
                        &__flag {
                            order: 1;
                        }
                    }
                }
            }
        }

        .swCheckBoxField {
            label {
                padding: @checkboxPaddingRtl;

                .label-icon {
                    left: unset;
                    right: 0;

                }
            }
        }
    }
}