
/*
================================================================================
|                                 CHECKBOX                                     |
================================================================================
*/
.lp-registration-form {
    .swCheckBoxField {
        label {
            color: @checkboxTextColor;

            .label-icon {
                border: @checkboxBorder;

                &__inner {
                    background-color: @checkboxTickBg;
                }
            }

            a {
                color: @checkboxLinkColor;

                &:hover {
                    color: @checkboxLinkColor;
                }
            }
        }
    }
}