.lp-registration-form {
    .swFormControl {
        border-color: @inputBorderColor;
        color: @inputTextColor;

        &:hover, &:focus {
            border-color: @inputFocusBorderColor;
        }

        &::placeholder {
            font-style: italic;
            font-weight: 600;
        }
    }
}