================================================================================
*/
.country-item {
    display: flex;
    align-items: flex-start;
    width: 100%;
    cursor: pointer;
    padding: @countryItemPadding;
    background: @countryItemBg;

    &:hover, &:focus, &[data-selected="true"] {
        background: @countryItemBgHover;
        outline: none;
    }

    &:focus {
        outline: auto;
    }

    &__name {
        margin-right: @countryItemNameMargin;
        font-size: @countryItemFontSize;
        font-weight: @countryItemFontWeight;
        line-height: @countryItemLineHeight;
        color: @countryItemTextColor;
    }

    &__code {
        font-size: @countryItemFontSize;
        font-weight: @countryItemFontWeight;
        line-height: @countryItemLineHeight;
        color: @countryItemPhoneExtTextColor;
    }

    &__text {
        flex: 1 1 auto;
        width: calc(100% - (@customSelectFlagIconWidth + @countryItemFlagMarginRight));
        line-height: @countryItemHeight;
    }

    &__flag {
        display: block;
        flex: 0 0 @customSelectFlagIconWidth;
        margin-top: @countryItemFlagMarginTop;
        margin-right: @countryItemFlagMarginRight;
        width: @customSelectFlagIconWidth;
        height: @customSelectFlagIconHeight;
        background-color: @customSelectFlagIconBgc;
        background-image: @countryItemFlagBgi;
        background-repeat: no-repeat;
        background-position: @countryItemFlagBgPos;
        box-shadow: @countryItemFlagBoxShadow;

        &[data-country="AF"] {
            height: 14px;
            background-position: -66px 0;
        }

